@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  line-height: 1;
  color: #333;
  font-family: 'Pretendard-Regular';
}


.tour_list {
  font-size: 20px;
  width: calc(100% - 30px);
  margin: 0 auto;
  word-break: keep-all;

  img {
    width: 80%;

    border-radius: 30px;
  }
}

.item {
  width: 40%;
  text-align: center;
  padding: 20px;
  margin-top: -40px;
  text-overflow: break-word;

  h4 {
    font-size: 26px;
    margin-bottom: 30px;
  }
}

.item:nth-child(1) {
  margin-top: 40px;
  background-color: #ffff9e;
  border-radius: 30px 0px 0px 30px;
  margin-left: auto;
}

.item:nth-child(2) {
  background-color: #ea94ff;
  border-radius: 0px 30px 30px 0px;
  margin-right: auto;
}

.item:nth-child(3) {
  background-color: #a894ff;
  border-radius: 30px 0px 0px 30px;
  margin-top: -150px;
  margin-left: auto;
}

.item:nth-child(4) {
  background-color: #bfff94;
  border-radius: 0px 30px 30px 0px;
  margin-right: auto;
}

.item:nth-child(5) {
  background-color: #ff8181;
  border-radius: 30px 0px 0px 30px;
  margin-top: -150px;
  margin-left: auto;
  margin-bottom: 150px;
}

.item--text {
  margin: 30px 0;

  p {
    margin-bottom: 30px;
  }

  span {
    font-size: 16px;
    padding: 5px 15px;
    background-color: #fff;
    border-radius: 20px;
  }
}

.wrap h1 span {
  display: inline-block;
  font-size: 20px;
  padding: 20px;
  margin: 10px;
  background-color: #fff;
  border-radius: 20px;
  background-color: #333;
  color: #fff;

}

h1 {
  color: #f397ff;
  padding: 100px;
  font-size: 40px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
text-align: center;
  .nav {
    width: calc(100%);
    padding: 0 40px;
    background: #fff;
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 100%;

    figure {
      margin-left: 20px;
      height: 80px;

      img {
        height: 100%;
      }
    }
  }

  .search {
    display: flex;
    justify-content: space-between;

    figure {
      height: 26px;
      margin-right: 20px;
      margin-left: 0px;

      img {
        height: 100%;
      }
    }
  }
  .bar{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50px;
   background-color: rgb(179, 255, 255);
    .end {
         
      margin: 0 auto;
      width: 50%;
      display: flex;
      justify-content: space-between;
      line-height: 60px;
    figure {
      
      height: 26px;
      margin-right: 20px;
      margin-left: 0px;

      img {
        height: 100%;
      }
    }
  }
  }
}

@media screen and (max-width: 767px) {
  .header {
    .nav {
      height: 80px;
      padding: 0 10px;
      width: calc(100%);
      background: #fff;
      align-items: center;
      display: flex;
      justify-content: space-between;

      figure {
        margin-left: 20px;
        height: 60px;

        img {
          height: 100%;
        }
      }
    }
  }

  .header {
    .nav {
      .search {
        figure {
          height: 26px;
          margin-right: 20px;
          margin-left: 0px;

          img {
            height: 100%;
          }
        }
      }
    }
  }

  .tour_list {
    font-size: 20px;
    width: calc(100% - 30px);
    margin: 0 auto;
    word-break: keep-all;

    img {
      width: 97%;

      border-radius: 20px;
    }
  }

  h1 {
    padding: 40px;
    padding-top: 100px;
    font-size: 26px;
    line-height: 1.6;
    text-align: center;
  }

  .item {
    width: 100%;
    text-align: center;
    padding: 20px 3px;
    text-overflow: break-word;

    h4 {
      font-size: 20px
    }
  }

  .item:nth-child(1),
  .item:nth-child(2),
  .item:nth-child(3),
  .item:nth-child(4),
  .item:nth-child(5) {
    margin-top: 40px;
    border-radius: 30px 30px 30px 30px;
  }

  .item--text {
    margin: 20px 0;

    p {
      margin-bottom: 20px;
      font-size: 20px;
    }

    span {
      font-size: 14px;
      padding: 5px 15px;
      background-color: #fff;
      border-radius: 20px;
    }
  }

  .wrap h1 span {
    display: inline-block;
    font-size: 16px;
    padding: 15px;
    margin: 5px;
    background-color: #fff;
    border-radius: 15px;
    background-color: #333;
    color: #fff;

  }
}